import tools from '@/common/tools.js'
import axios from 'axios'

export default {
  name: 'returngoods',
  data: function() {
    return {
      recId: '',
      allData: {}
    }
  },
  activated() {
    this.recId = this.$route.query.recId
    console.log(this.recId)
    this._getReturnGoods()
  },
  methods: {
    gotoTuikuan(num) {
      this.$router.push({path: 'applyreturn', query: {'recId': this.recId, 'returnType': num}})
    },
    _getReturnGoods() {
      let url = '/Mobile/Order/return_goods1.html'
      axios.get(url, {
        params: {
          rec_id: this.recId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            this.allData = res.data.data
          } else {
            tools.showError(res.data.msg)
            setTimeout(() => {
              this.$router.go(-1)
            }, 2000)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}



